import React, { useState } from 'react'
import { useSpring, animated, config } from 'react-spring'

const HomePage = () => {

    const fadeIn = useSpring({
        opacity: 1,
        transform: 'translateY(0%)',
        from: { opacity: 0, transform: 'translateY(-100%)' },
        delay: 400,
        config: { 
            ...config.wobbly
        }, 
    });

    const fadeInImg = useSpring({
        opacity: 1,
        y: 0,
        transform: 'translateY(0%)',
        from: { opacity: 0, y: 500, transform: 'translateY(50%)' },
        delay: 400,
        config: {
                mass: 1.1,
                tension: 60,
                friction: 14,
                precision: 0.001,
                velocity: 0.018
              },
    });

    return (
        <div className="h-screen w-full flex flex-col items-center justify-start">
            <animated.div className="select-none text-center leading-none font-title text-3xl mt-20 text-gray-800" style={{lineHeight:'33px', fontSize:'48px', ...fadeIn}}>
                Sommelier<br /><span className="leading-none font-title text-2xl" style={{lineHeight:'33px', fontSize:'40px'}}>du dimanche</span>
            </animated.div>
            <animated.img style={{height:'85vh', bottom: '-11vh', ...fadeInImg}} alt="14426117" className="absolute bottom-0" src="./images/14426117.png" />
        </div>
    );
}

export default HomePage;